import React from "react";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { connect } from "react-redux";
import { useFirestoreConnect } from "react-redux-firebase";
const useStyles = makeStyles((theme) => ({
  paper: {
    height: "100%",
    padding: "20px",
    display: "flex",
    justifyContent: "center",
    textAlign: "left",
    [theme.breakpoints.down("md")]: {
      borderRadius: 0,
    },
    paddingTop: "80px",
  },
  container: {
    minHeight: "100%",

    [theme.breakpoints.down("md")]: {
      padding: 0,
    },
  },
  content: {
    maxWidth: "850px",
    [theme.breakpoints.down("md")]: {
      maxWidth: "700px",
    },
  },
  image: {
    display: "inline-block",
    width: "100%",
    marginBottom: "20px",
  },
}));

const Post = ({ post }) => {
  useFirestoreConnect([{ collection: "posts" }]);
  const classes = useStyles();
  return (
    <Container maxWidth="lg" className={classes.container}>
      {post ? (
        <Paper elevation={0} className={classes.paper}>
          <div className={classes.content}>
            <Typography component="p">
              {`${post.authorFirstName} ${post.authorLastName}`} 1hr. ago
            </Typography>
            <Typography component="h4" variant="h4">
              {post.title}
            </Typography>
            <img
              src="https://leonardo.osnova.io/f7811be6-a947-fc4b-adf9-e65c6669de67/-/resize/2100/"
              alt=""
              className={classes.image}
            />
            <Typography component="p">{post.content}</Typography>
          </div>
        </Paper>
      ) : null}
    </Container>
  );
};

const mapStateToProps = (state, ownProps) => {
  if (state.firestore.ordered.posts) {
    return {
      post: state.firestore.ordered.posts.find(
        (post) => post.id == ownProps.match.params.id
      ),
    };
  } else return { post: null };
};

export default connect(mapStateToProps)(Post);
